import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom/client';
import VacFormPresenter from './presenter/vac-form-presenter';
import VacancyModel from './model/vac-model';

const root = ReactDOM.createRoot(document.getElementById('root'));

// Создаём экземпляр модели
const vacancyModel = new VacancyModel();

const App = () => {
  const [mode, setMode] = useState('view'); // Режим по умолчанию 'view'
  const [selectedVacancy, setSelectedVacancy] = useState(null); // Для выбранной вакансии при редактировании

  // Переключение между режимами
  const handleViewClick = () => setMode('view');
  const handleAddClick = () => setMode('add');
  const handleEditClick = (vacancy) => {
    setSelectedVacancy(vacancy); // Передаем данные вакансии для редактирования
    setMode('edit');
  };

  // Устанавливаем слушатели для кнопок, после того как компонент монтируется
  useEffect(() => {
    const viewButton = document.getElementById('viewBtn');
    const addButton = document.getElementById('addBtn');

    // Слушатели событий для кнопок
    viewButton.addEventListener('click', handleViewClick);
    addButton.addEventListener('click', handleAddClick);

    // Очистка слушателей при размонтировании компонента
    return () => {
      viewButton.removeEventListener('click', handleViewClick);
      addButton.removeEventListener('click', handleAddClick);
    };
  }, []);

  return (
    <VacFormPresenter
      mode={mode}
      vacancyModel={vacancyModel} // Передаем модель
      selectedVacancy={selectedVacancy}
      onEditClick={handleEditClick}
    />
  );
};

root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
